import {
	classNames,
	widgetSettings
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { IWidgetSetting } from '../app/app';
import { observer } from 'mobx-react';
import { registerUpdate, unregisterUpdate } from '../../lib/r3e';
import React from 'react';
import style from './clock.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Clock extends React.Component<IProps, {}> {

	@observable
	localTime = new Date();

	@observable
	currentTime = String('00' + this.localTime.getHours()).slice(-2)
		+ ':'
		+ String('00' + this.localTime.getMinutes()).slice(-2)
		+ ':'
		+ String('00' + this.localTime.getSeconds()).slice(-2);

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		this.updateTheTime();
	};

	private updateTheTime() {

		this.localTime = new Date();
		this.currentTime = String('00' + this.localTime.getHours()).slice(-2)
		+ ':'
		+ String('00' + this.localTime.getMinutes()).slice(-2)
		+ ':'
		+ String('00' + this.localTime.getSeconds()).slice(-2);
	}

	render() {
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.theClock, this.props.className, {
					shouldShow: 1 === 1
				})}
			>
				{/* Clock */}
				<div className="theTime">
					{this.currentTime}
				</div>
			</div>
		);
	}
}
